.infoBox{
    flex:1;
    cursor: pointer;
}

.infoBox:not(:last-child){
    margin-right:15px;
}

.infoBox_cases{
    color: lightgreen;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}
 
.infoBox_total{
    color:#6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
}

.infoBox--selected{
    border-top: 10px solid greenyellow;
}

.infoBox--red {
    border-color: red;
}
.infoBox--orange{
    border-color: orange;
}

.infoBox_cases--orange{
    color:  orange !important;
}

.infoBox_cases--red{
    color:  red !important;
}
@media (max-width: 990px){
    .infoBox_cases{
        font-weight: 600;
        font-size: 1.1rem;
        margin-bottom: 0.5rem;
        margin-top: 0.5rem;
    }
    .infoBox_title{
        font-size: 0.8rem !important;
        text-align: center !important;
        font-weight: bolder !important;
    } 
  }